html,
body {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  color: #333;
  background: #fff;
  font-size: 15px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
p {
  margin-bottom: 0;
}
a {
  color: #333;
}
a:hover,
a:focus {
  color: #d73630;
  text-decoration: none;
}
ul {
  padding: 0;
  list-style: none;
  margin-bottom: 0;
}
li {
  list-style: none;
}
.bg-light {
  background: #fff !important;
}

/* my css */
header {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 99;
  background: #ddd;
}
.topHeader {
  background-color: #333;
  color: #fff;
  width: 100%;
  position: block;
  text-align: center;
}
.topHeader ul li {
  display: inline-grid;
}
.topHeader ul li a {
  padding: 5px 15px;
  color: #fff;
  border-right: 1px solid #fff;
}
.navbar {
  box-shadow: 0 0 15px #00000047;
}
.navbar-expand-lg .navbar-nav .nav-link {
  text-transform: uppercase;
  font-weight: 600;
}
.navbar-light .navbar-nav .active > .nav-link,
.navbar-light .navbar-nav .nav-link.active,
.navbar-light .navbar-nav .nav-link.show,
.navbar-light .navbar-nav .show > .nav-link {
  color: #d73630;
}

footer .mainFooter {
  padding: 35px 15px;
  background-color: #333;
  color: rgb(201, 201, 201);
}
footer h5 {
  color: #fff;
}
footer a {
  color: rgb(201, 201, 201);
}
footer .footer-list-menu {
  padding-left: 15px;
}
.footer .footer-widget .footer-list-menu li:before {
  content: "";
  position: absolute;
  width: 5px;
  height: 5px;
  left: 0;
  top: 12px;
  background: #d73630;
  border-radius: 100%;
}

.design a {
  display: inline-grid;
  box-shadow: 0px 0px 2px #d73630;
  border-bottom: 2px solid #d73630;
  text-align: center;
  padding: 5px 10px;
  text-decoration: none;
  color: #fff;
  border-radius: 5px;
  font-weight: 600;
  margin: 2px 2px;
}
.design a:hover {
  background-color: #d73630;
}

.social-icons {
  margin: 0;
  padding: 0;
  width: auto;
}
.social-icons li {
  display: inline-block;
  margin: -1px 1px 5px 0;
  padding: 0;
  border-radius: 100%;
  overflow: visible;
  box-shadow: 0 2px 2px 0 rgb(0 0 0 / 30%);
  margin-right: 3px;
}
.social-icons li.social-icons-facebook a {
  background: #3b5a9a;
}
.social-icons li.social-icons-twitter a {
  background: #1aa9e1;
}
.social-icons li.social-icons-linkedin a {
  background: #0073b2;
}
.social-icons li.social-icons-instagram a {
  background: #7c4a3a;
}
.social-icons li a {
  background: #171717;
  color: #fff !important;
  font-size: 25px;
}
.social-icons li a {
  -webkit-transition: all 0.2s ease;
  -moz-transition: all 0.2s ease;
  transition: all 0.2s ease;
  background: #fff;
  border-radius: 100%;
  display: block;
  height: 50px;
  line-height: 50px;
  width: 50px;
  text-align: center;
  text-decoration: none;
}
.social-icons li a:hover {
  opacity: 0.8;
}
.bg_copyright {
  background-color: rgb(65, 65, 65);
  color: #6b6b6b;
  padding: 20px 0;
}
.footer .copyright-section {
